import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {PostManagementComponent} from './post/post-management/post-management.component';
import {UserListComponent} from './user/user-list/user-list.component';
import {PostDetailComponent} from './post/post-detail/post-detail.component';
import {UserDetailComponent} from './user/user-detail/user-detail.component';
import {UserEditProfileComponent} from './user/user-edit-profile/user-edit-profile.component';
import {UserCreateNewComponent} from './user/create-new/user-create-new.component';
import {UserSecurityComponent} from './user/user-security/user-security.component';
import {HomeComponent} from './layout/home/home.component';
import {LoginComponent} from './layout/login/login.component';
import {LogoutComponent} from './layout/logout/logout.component';
import {AuthGuard} from './api/util/auth-guard.service';
import {RoleGuard} from './api/util/role-guard.service';

const routes: Routes = [
  {path: '', component: HomeComponent, canActivate: [AuthGuard], data: {breadcrumb: 'Trang chnh', title: 'Trang chnh'},
    children:[

      {path: 'users', component: UserListComponent, canActivate: [RoleGuard],
        data: {breadcrumb: 'Qun l ngi dng', title: 'Danh sch ngi dng'}},
      {path: 'users', canActivateChild: [RoleGuard],
        data: {breadcrumb: 'Qun l ngi dng', title: 'Danh sch ngi dng'},
        children: [
          {path: ':id/detail', component: UserDetailComponent, data: {breadcrumb: 'Thng tin c nhn', title: 'Thng tin ngi dng'}},
          {path: ':id/detail', data: {breadcrumb: 'Thng tin c nhn', title: 'Thng tin ngi dng'},
            children: [
              {path: 'edit', component: UserEditProfileComponent, data: {breadcrumb: 'Chnh sa thng tin', title: 'Chnh sa thng tin ngi dng'}},
              {path: 'security', component: UserSecurityComponent, data: {breadcrumb: 'Bo mt ti khon', title: 'Thay i mt khu'}}
            ]},
          {path: 'add', component: UserCreateNewComponent, data: {breadcrumb: 'To mi ngi dng', title: 'To mi ngi dng'}}
        ]
      },

      {path: 'posts', component: PostManagementComponent, data: {breadcrumb: 'Qun l bi ng', title: 'Danh sch bi ng'}},
      {path: 'posts', data: {breadcrumb: 'Qun l bi ng', title: 'Danh sch bi ng'},
        children: [
          {path: ':id/detail', component: PostDetailComponent, data: {breadcrumb: 'Chi tit bi ng', title: 'Chi tit bi ng'}}
        ]},
      {path: '', redirectTo: '/posts', pathMatch: 'full'}
    ]
  },

  {path: 'login', component: LoginComponent},
  {path: 'logout', component: LogoutComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
